html,body{
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
  font-size: 16px;
  overflow-x: hidden;
  scroll-behavior: smooth;
  font-family: Hiragino Kaku Gothic Pro W6;
}

a{
  color: none;
  outline: none;
  text-decoration: none;
}

h1{
  /* width: 100%; */
  font-size: 1.7em;
  margin: 0;

  
}
/* hero section */
#main-header-container{
  background: 
  /* black overlay */ linear-gradient(
      rgba(0, 0, 0, 0),
      rgba(0, 0, 0, 0)
    ),
    /* bottom, image */ url("assets/header.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
}

#logo-container{
  display: flex;
  justify-content: center;
  align-self: center;
}

#logo{
  align-items: center;
  width: 65%;
  width: 209px;
  height: 220px;
}
#social-media-container{
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  /* width: 100%; */

  position: absolute;
  bottom: 15%;
  left: 2%;
}

.social-media-icons{
  width: 2%;
  margin-top: 5px;
  min-width: 30px;
  cursor: pointer;
  position: relative;
  left: 0px;
  transition: all 250ms cubic-bezier(0.02, 0.01, 0.47, 1);

}

.social-media-icons:hover{
  position: relative;
  left: 8px;
}

#blue-navbar{
  
  height: 110px;
  width: 100%;
  /* z-index: 100; */
  /* background: #73D3D2; */
  bottom: 0;
  position: absolute;
  
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;

}

#dots-image{
  position: absolute;
  width: 20%;
  right: 15%;
  top: 9%;
}

#see-more-text{
  background-color: #FF8900;
  transform: rotate(90deg);
  color: white;
  padding-left: 10px;
  padding-right: 10px;
}

/* video section */
#video-section-container{
  /* background-color: #73D3D2; */
  background: linear-gradient(#73D3D2, white);
  color: white;
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  font-size: 2em;
  

  /* margin-bottom: 6%; */
  padding: 15%;
  padding-top: 0%;
  padding-bottom: 0;

}

#video-header-container{
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 10%;
  margin-bottom: 0%;
}

#iframe-container{
  position: relative;
  width: 100%;
  padding-bottom: 55.25%;
  /* -webkit-box-shadow: 2px 3px 4px 1px #b6b6b6; */
  /* box-shadow: 2px 3px 4px 1px #b6b6b6; */
}

#iframe-container iframe{
  position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.orange-text{
  color: #FF8900;
}

/* gallery section */
#gallery-main-container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 10%;
}

.image-children{
  width: 100%;
  max-width: 300px;
  
}

/* contact */
#main-contact-container{
  padding: 0 15% 0 15%;
  margin-top: 10%;
  /* margin-bottom: 10%; */
}

.svg-container{
  width: 50%;
}
.contact-paragraphs{
  margin-bottom: 10%;
}

#form-container{
  display: flex;
  flex-direction: row;
  width: 100%;
}

#input-container{
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  width: 100%;
  height: 140px;
}

input,textarea{
  outline: none;
  border-radius: 10px;
  border: 1px solid rgb(179, 179, 179);
}

input{
  
  height: 30px;
  padding: 3%;
  
  
}
textarea{
  resize: none;
  width: 100%;
  padding: 3%;
  margin-left: 5%;
  
}

#bottom-bar{
  height: 200px;
  background-color: #73D3D2;
}

::placeholder{
  color: #ccc;
}

@media only screen and (max-width: 600px){
  h1{
    font-size: 0.9em;
    width: 100%;
  }

  #logo{
    width: 50%;
  }

  .svg-container{
    width: 100%;
  }
  #form-container{
    flex-wrap: wrap;
  }

  #input-container{
    height: 110px;
  }

  textarea{
    margin-top: 5%;
    margin-left: 0;
  }
}

/* contact button */
#contact-button-container{
  text-align: right;
}

#contact-button{
  color: #FF8900;
  border: none;
  font-weight: bold;
  font-size: 1em;
  cursor: pointer;
  outline: none;
  background: none;
}

/* navbar */
#navbar-list{
  padding: 0;
  display: flex;
  justify-content: flex-end;
  width: 100%;
  margin: 0 auto;
  padding-top: 2%;
  padding-bottom: 2%;
  position: fixed;
  top: 0;
  /* background-color: #FF8900;
  background-color: #73D3D2;
  background-color: rgb(104, 196, 195);
  background-color: white; */
  z-index: 100;
  border-bottom: 0px solid #FF8900;
  transition: 0.2s ease-in-out;
}

#navbar-list:hover{
  /* border-bottom: 5px solid #FF8900; */
}

#navbar-list li{
  list-style: none;
  margin-right: 5%;
  text-decoration: none;
}



#navbar-list a, .slidebar-menu-items a{
  color: white;
  color: #FF8900;
  opacity: 0.6;
  text-decoration: none;
  transition: 0.2s ease-in-out;
}

#navbar-list a:hover{
  opacity: 1;
}

.activeRoute{
  color: #FF8900;
  opacity: 1 !important;
}

/* hamburger menu */
.hamburger-container{
  display: none;
  position: absolute;
  top: 3%;
  right: 7%;
  z-index: 500;
  border-radius: 3px;
  background: #99faf9;
}

.hamburger-container > div{
  /* display: flex;
  justify-content: center;
  align-items: center;
  width: 100% !important;
  height: 100% !important; */
}

.ham-nav-items{
  display: none;
  position: absolute;
  top: 0;
  z-index: 100;
  margin: 0 auto;
  padding: 0;
  height: 100%;
  background: #aef7ff;
  right: -100%;
  width: 100%;
  z-index: 100;
  
}


.slidebar-list-container{
  display: flex;
  justify-content: center;
  height: 100%;
  flex-direction: column;
  background: darkgrey;
}
.slide-in{
  right: 0;
  display: block;
  animation: slide-in ease-in-out;
  animation-duration: 0.5s;
}

.slide-none{
  display: none;
}



.slide-out{
  
  animation: slide-out ease-in-out;
  animation-duration: 0.5s;
}

#slidebar-list{
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  height: 100%;
}

.slidebar-menu-items{
  height: 100%;
  width: 100%;
  background: #99faf9;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s ease-in-out;
}

.slidebar-menu-items a{
  height: 100%;
  width: 100%;
  
}



.slidebar-menu-items:hover{
  background-color: #c4fffe;
}

@keyframes slide-in{
  0%{
    right: -100%;
  }
  100%{
    right:0
  }
}

@keyframes slide-out{
  0%{
    right: 0;
  }
  100%{
    right:-100%
  }
}


@media only screen and (max-width: 600px){
    #main-contact-container{
      margin-top: 0;
    }

    .hamburger-container{
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;
      z-index: 1000;
    }

    #navbar-list{
      display: none;
    }
}
@media only screen and (min-width: 600px){
    #hamburger-slide-container, .slidebar-list-container{
      display: none;
    }

    #navbar-list{
      display: flex;
    }
}
